import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService, BsModalRef, ModalOptions } from 'ngx-bootstrap/modal';

export interface SelectTypeDropdown {
  id: number;
  name: string;
}
@Component({
  selector: 'app-documents',
  templateUrl: './documents.component.html'
})
export class DocumentsComponent implements OnInit {
  public asideIsShow = false;
  public isDsc = false;
  private html;
  public showFeedback;
  public selectTypeDropdown: SelectTypeDropdown[] = [
    { id: 1, name: 'Request to change bank document' },
    { id: 2, name: 'General' },
    { id: 3, name: 'Type-1' },
  ];
  public selectedTypeDropdown = 'Request to change bank document';

  modalRef: BsModalRef;
  config = {
    backdrop: true,
    ignoreBackdropClick: false
  };
  constructor(private modalService: BsModalService) {}

  openModal(templateConfirmation: TemplateRef<any>) {
    this.html.classList.add('is-show-modal');
    this.modalRef = this.modalService.show(
      templateConfirmation,
      Object.assign({}, {class: 'anyday-modal dashboard-modal confirmation-modal modal-lg'})
    );
  }

  ngOnInit(): void {
    this.html = document.getElementsByTagName('html')[0];

    this.modalService.onHide
      .subscribe(() => {
        this.html.classList.remove('is-show-modal');
      });
  }
  openAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.add('aside-open');

    const asidebar = document.getElementsByClassName('asidebar')[0];
    asidebar.scroll(0, 0);
  }

  closeAside(): void {
    const body = document.getElementsByTagName('html')[0];
    body.classList.remove('aside-open');
    const asidebar = document.getElementsByClassName('asidebar');
  }

  toggleSort(): void {
    this.isDsc = !this.isDsc;
  }

  toShowFeedback(): void {
    this.showFeedback = true;
  }

}
